          <img src="../../assets/contactus/icon-twi-grey.png" alt="">
        </div>
        <div>
          <img src="../../assets/contactus/icon-insta-grey.png" alt="">
        </div>
      </div>
      <div class="wrapper">
        <img src="../../assets/article/img-news-content.png" alt="">
        <p v-for="(item, index) in article">{{item}}</p>
        <div class="time">{{$t('m.publish')}}2017.10.8</div>
      </div>
    </div>
  </div>
