          '此前一个月，微软也公布了类似的消息。凭借Azure“政府机密”服务，微软的政务云将可以为情报部门及其合作伙伴提供计算服务，处理被归类为“机密”的数据。',
          'AWS全球公共部门副总裁特雷萨·卡尔森（Teresa Carlson）表示：“美国情报机构目前可以借助通用工具、不断改进的新技术，以及灵活的规模来执行计算任务。亚马逊3年前推出了AWS‘绝密区域’，吸引了美国情报界客户，并获得了巨大的成功。最终，这种能力将推动更多部门展开合作，帮助决策者更快地获得关键信息，有利于我们的国家安全。”',
