      }
    },
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>
<style scoped lang='stylus'>
  @import '../../common/stylus/mixin.styl'
  .article
    margin-top 24px
    .title
      margin-top 42px
      font-family: MicrosoftYaHei;
      font-size: 24px;
      font-weight: bold;
      line-height: 0.67;
      text-align: left;
      color: #333333;
    .share
      float right
      margin-top -24px
      img
        width auto
        height auto
        vertical-align middle
      div
        display inline-block
        width 24px
        height 24px
        border 2px solid  #555;
        border-radius 50%
        text-align center
        margin-right 24px
        cursor pointer
        &:last-child
          margin-right 0
    .wrapper
      background #f3f3f3
      padding-bottom 154px
      img
        margin-top 20px
        width 1200px
        height 300px
        margin-bottom 12px
      p
        font-size 14px
        line-height 24px
        text-indent 2em
        margin 24px 0
        padding 0 15px
      .time
        float right
        margin-top 72px
        margin-right 15px

</style>


